import { useState, useEffect} from 'react'
import './App.css';
import Header from './components/Header'
import Tasks from './components/Tasks'
import Addtask from './components/Addtask'
import axios from 'axios'
import { FaRegNewspaper } from 'react-icons/fa';

const initialState = '';

function App() {
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks, setTasks] = useState(initialState)

  useEffect(() => { 
    const fetchTasks = async () => { 
      try{
        const res = await fetch('http://localhost:5000/tasks');
        const data = await res.json();
        return data;
      } catch( error){
        console.log( error);
      }
    }

    fetchTasks()
      .then( (responde) => { 
        setTasks( responde.data)
      })



    /* axios.get('http://localhost:5000/tasks')
      .then( (respond) => { 
        setTasks(respond.data)
      }) 
      .catch((err) => { 
        console.log('Something went wrong');
      }) */
  }, [])
  // delete task
  const deleteTask = (key) => { 
    let remainTask = tasks.filter( task => task.id !== key)
    setTasks( tasks.filter( task => task.id !== key))
  }

  // toggle reminder
  const toggleReminder = (id) => { 
    setTasks( tasks.map( (task) => task.id === id ? {...task, reminder: !task.reminder} : task))
  }


  // add Task
  const addTask = (task) => { 
    setTasks( [...tasks, {id: tasks.length + 1, ...task}])
  }

  
  // the props with onX has meaning is
  return (
    <div className="container">
      <Header 
        onAdd={ () =>  setShowAddTask( !showAddTask)}
        showAdd={ showAddTask} 
      />
      {showAddTask &&  <Addtask onSubmit={ (task) => addTask(task)}/>}

      { tasks.length > 0 ?
        <Tasks 
          tasks={tasks} 
          onDelete={deleteTask} 
          onToggle={toggleReminder}
        /> :
        'No Tasks to Show'
      }

    </div>
  );
}


